.card {
  border: 2px solid rgb(201, 201, 201);
  border-radius: 10px;
  background-color: rgb(201, 201, 201);
  box-shadow: 5px 10px 10px #888888;
  width: 420px;
  height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  padding-top: 20px;
  margin: 20px;

  &__img {
    width: 100px;
    height: 300px;
  }
}
